import React from 'react';
import '../css/Home.css';
import Header from './Header';
import Footer from './Footer';
import Sec_section from './Sec_section';
import Third_section from './Third_section';
import Fourth_section from './Fourth_section';
import Fifth_section from './Fifth_section';
import Seventh_section from './Seventh_section';
import Ninth_section from './Ninth_section';
import Car from './Car';
import { HashRouter,Route,Routes, BrowserRouter } from 'react-router-dom'; 
import { connect             }  from "react-redux";
import gsap from "gsap";
import { compose } from 'redux';



const mapStateToProps = state => {
    return {  
        lastPathUrl               : state.lastPathUrl,
  
          };
  };



class connectedHome extends React.Component {
    constructor(props) {
        super(props)
    
        this.state = {
            prevPath: '',
            fiat: false,
            bmw: false,

        }
    }


    componentDidMount() {
      
            this.mainContainerScroll();
             
           
   
    }

    componentDidUpdate(prevProps) {
        // let currentPathUrl = window.location.href.substring(window.location.href.lastIndexOf('/') + 1);
        // Listen to params url id changes and if prevState id !== this.id, call function
      
        if(prevProps.lastPathUrl !== this.props.lastPathUrl) {
           this.setState({ prevPath: this.props.lastPathUrl })
        }
      }
   
   
      mainContainerScroll(e) {
        
           // check if is view
        const DivIsInView = el => {
        const scroll = window.scrollY || window.pageYOffset;
        const boundsTop = el.getBoundingClientRect().top + scroll;
        
        const viewport = {
          top: scroll,
          bottom: scroll + window.innerHeight,
        }
          const bounds = {
          top: boundsTop,
          bottom: boundsTop + el.clientHeight,
        }
    
        return ( bounds.bottom >= viewport.top && bounds.bottom <= viewport.bottom ) 
          || ( bounds.top <= viewport.bottom && bounds.top >= viewport.top );
        }


        

       
        // // First section
        let firstSec = document.querySelector('.f_cont_wrapper');
        let firstSecInview = DivIsInView(firstSec);

            if(firstSecInview) {
                let fSecTyTxt = document.querySelector('.fseculbtxt_tyk');
                if(!fSecTyTxt.classList.contains('fseculbtxt_tyk_show')) {
                    document.querySelector('.fseculbtxt_tyk').classList.add('fseculbtxt_tyk_show');
                    document.querySelector('.fseculbtxt_jgmst').classList.add('fseculbtxt_jgmst_show');
                    document.querySelector('.fseculbtxt_tkagain').classList.add('fseculbtxt_jgmst_show');
                    document.querySelector('.fseculbtxt_expmor').classList.add('fseculbtxt_jgmst_show');
                    document.querySelector('.ful_bgsec_arr').classList.add('fseculbtxt_jgmst_show');
                }
            }
            
        // Second section
    
        let seCSec = document.querySelector('.s_sec_video');
        let seCSecInview = DivIsInView(seCSec);

        if(seCSecInview) {
            document.querySelector('.s_sec_slsh').style.left = 0;
        }

        // Third section

     
        let thirdCSec = document.querySelector('.th_wrhlf_f_txt');
        let thirdCSecInview = DivIsInView(thirdCSec);

        if(thirdCSecInview) {
            // Animate third first text
            let thrdFTxt = document.querySelectorAll('.th_wrhlf_f_txt');
            thrdFTxt.forEach(el => el.classList.add('fseculbtxt_jgmst_show'));
            // Animate third secont txt
            let thrdFTxtTwo = document.querySelector('.thrd_thrd_wrhalf_wrap');
            thrdFTxtTwo.classList.add('fseculbtxt_jgmst_show');
        }

         // Fourth section

     
         let fourthGlass = document.querySelector('.fth_fth_wrhalf_wrpgls');
         let fourthGlassInview = DivIsInView(fourthGlass);
 
         if(fourthGlassInview) {
       
             // Animate right bottle
             if(!document.querySelector('.fth_fth_s_wrbttl').classList.contains('fth_rightbottle_show')) {
                document.querySelector('.fth_fth_s_wrbttl').classList.add('fth_rightbottle_show');

                let fthSpanTxts = document.querySelector('.fth_fth_wrhalf_f').getElementsByTagName('span');

                 for(let i =0; i< fthSpanTxts.length; i++) {
                     fthSpanTxts[i].classList.add('fthspantxt_show');
                 }
                 // Reveal ice glass
                 document.querySelector('.fth_fth_wrhalf_wrpgls').classList.add('fthspantxt_show');
            }
         }

         // Seventh section

     
         let svthTitle = document.querySelector('.svth_half_sc_tittle');
         let svthTitleInview = DivIsInView(svthTitle);
 
         if(svthTitleInview) {
            console.log('sloboz');
             // Animate text
             if(!svthTitle.classList.contains('svth_txtshow')) {
                svthTitle.classList.add('svth_txtshow');
                document.querySelector('.svth_half_sc_tittle_p').classList.add('svth_txtshow');
                document.querySelector('.svth_half_btnlnk_wrap').classList.add('svth_txtshow');
            }
         }



      }

    render() {
        return (
                <div>
            
                    <div className='home_container'>
                        <div className='wrap_home_container' onScroll={(e) => this.mainContainerScroll(e)}>
                            <div className='home_section'>
                                <div className='home_content'>

                                <Car/>
                        
                                </div>
                            </div>     

                            <div className='home_section'>
                                <div className='home_content'>
                                    <Sec_section/>
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                <Third_section/>
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                <Fourth_section/>
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                <Fifth_section/>
                                </div>
                            </div>

                            <div className='home_section m_section_sixthsec'>
                                <div className='home_content m_content_sixthsec'>
                                
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                    <Seventh_section/>
                                </div>
                            </div>

                            <div className='home_section m_section_eight'>
                                <div className='home_content'>
                                <iframe src="https://www.youtube.com/embed/FXnIIGYkmN8" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                 <Ninth_section/>
                                </div>
                            </div>

                            <div className='home_section'>
                                <div className='home_content'>
                                
                                </div>
                            </div>

                             <Footer/>
                        </div>
                    </div>
                </div>
             
        )
    }
}


const Home = connect(mapStateToProps,null)(connectedHome);
export default Home;
 
