import React from 'react';
import '../css/Header.css';
import { Link }  from 'react-router-dom'
import { setPathUrl } from '../actions'; 
import { connect             }  from "react-redux";




const mapStateToProps = state => {
    return {  
        lastPathUrl               : state.lastPathUrl,

          };
  };

  
function mapDispatchToProps(dispatch) {
    return {
        setPathUrl : bol    => dispatch(setPathUrl(bol))
          };
  }




class connectedHeader extends React.Component {
    constructor(props) {
        super(props)
    
        this.state = {
 
        }
    }

    componentDidMount() {
        window.addEventListener('resize', (e) => this.closeHambMenu(e));
    }
    
    closeHambMenu(e) {
        let hambMenu = document.querySelector('.head_burgermenu');
        let mobMenu = document.querySelector('.h_mobmenu_left');
        // If mob menu is open, turn off.
        if(window.innerWidth > 600 && mobMenu.classList.contains('hmobmen_active')) {
            mobMenu.classList.remove('hmobmen_active');
            hambMenu.classList.remove('active');
        }
    }
 

 
openMobMenu() {
    let hambMenu = document.querySelector('.head_burgermenu');
    let mobMenu = document.querySelector('.h_mobmenu_left');
    
    // Show / hide hamb menu; Animate on/off hamb button
    if(!hambMenu.classList.contains('active')) {
        mobMenu.classList.add('hmobmen_active');
        hambMenu.classList.add('active');     
    } else {
        mobMenu.classList.remove('hmobmen_active');
        hambMenu.classList.remove('active');
    }

}


 
    render() {
        return (
                <div>
                    <div className='header_container'>
                        <div className='head_contwrap'>
                            <div className='head_cont_wraplogo'></div>

                            <div className='head_cont_wrapnav'>
                                <Link to={'/'}>Home</Link>
                                <Link to={'/products'}>Products</Link>
                                <Link to={'/shop'}>Shop</Link>
                                <Link to={'/contact'}>Contact</Link>
                                <Link to={'/cart'}>Cart</Link>
                            </div>

                            <div className='head_cont_wrhmbbtn'>
                                
                            <div className='head_burgermenu' onClick={() => this.openMobMenu()}> 
                                <span className='burger_line burger_line-top'></span>
                                <span className='burger_line burger_line-center'></span>
                                <span className='burger_line burger_line-bottom'></span>
                            </div>
                            </div>
                        </div>
                        {/* Mobile dropmenu */}
                        <div className='h_mobmenu_left'>
                            <div className='h_mobmenu_left_wrapnav'>
                                <Link to={'/'} onClick={() => this.openMobMenu()}>Home</Link>
                                <Link to={'/products'} onClick={() => this.openMobMenu()}>Products</Link>
                                <Link to={'/shop'} onClick={() => this.openMobMenu()}>Shop</Link>
                                <Link to={'/contact'} onClick={() => this.openMobMenu()}>Contact</Link>
                                <Link to={'/cart'} onClick={() => this.openMobMenu()}>Cart</Link>
                            </div>
                            <div className='h_mobmenu_left_wrapsocial'>
                            <div className='fcwrp_ssec_wrapsociallinks'>
                                    <a href='#'></a>
                                    <a href='#'></a>
                                    <a href='#'></a>
                                    <a href='#'></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        )
    }
}

const Header = connect(mapStateToProps,mapDispatchToProps)(connectedHeader);
export default Header;
